<template>
  <div id="home-component" class="container-fluid" style="background: #e3e5eb;">
    <div class="row" style="background: #e3e5eb;">
      <div class="col-lg-12 col-md-12 col-sm-12 pt-0 pb-0">
        <div class="d-flex flex-column flex-nowrap align-items-center">
          <TitleCard icon="calendar_today" title="Mi horario" :dark="true" class="btn-block" />

          <div class="card w-100" style="border-radius: 0;">
            <v-container class="w-100 d-flex flex-column align-items-center">
              <v-sheet class="w-100" max-width="1000" height="600">
                <v-calendar
                  ref="calendar"
                  v-model="calendario"
                  :type="type"
                  :start="start"
                  :end="end"
                  :min-weeks="minWeeks"
                  :max-days="maxDays"
                  :now="now"
                  :dark="dark"
                  :weekdays="weekdays"
                  :first-interval="intervals.first"
                  :interval-minutes="intervals.minutes"
                  :interval-count="intervals.count"
                  :interval-height="intervals.height"
                  :interval-style="intervalStyle"
                  :show-interval-label="showIntervalLabel"
                  :short-intervals="shortIntervals"
                  :short-months="shortMonths"
                  :short-weekdays="shortWeekdays"
                  :color="color"
                  :events="events"
                  :event-overlap-mode="mode"
                  :event-overlap-threshold="45"
                  :event-color="getEventColor"
                  :weekday-format="getDay"
                ></v-calendar>
              </v-sheet>
            </v-container>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import TitleCard from "../../../components/IntranetComponents/Cards/TitleCard";

const weekdaysDefault = [0, 1, 2, 3, 4, 5, 6];

const intervalsDefault = {
  first: 0,
  minutes: 60,
  count: 24,
  height: 48
};

const stylings = {
  default(interval) {
    return undefined;
  },
  workday(interval) {
    const inactive =
      interval.weekday === 0 ||
      interval.weekday === 6 ||
      interval.hour < 9 ||
      interval.hour >= 17;
    const startOfHour = interval.minute === 0;
    const dark = this.dark;
    const mid = dark ? "rgba(255,255,255,0.1)" : "rgba(0,0,0,0.1)";

    return {
      backgroundColor: inactive
        ? dark
          ? "rgba(0,0,0,0.4)"
          : "rgba(0,0,0,0.05)"
        : undefined,
      borderTop: startOfHour ? undefined : "1px dashed " + mid
    };
  },
  past(interval) {
    return {
      backgroundColor: interval.past
        ? this.dark
          ? "rgba(0,0,0,0.4)"
          : "rgba(0,0,0,0.05)"
        : undefined
    };
  }
};

export default {
  components: {
    TitleCard
  },
  data: () => ({
    dark: false,
    calendario: "",
    startMenu: false,
    start: "2020-03-01",
    endMenu: false,
    end: "2020-12-30",
    nowMenu: false,
    minWeeks: 1,
    now: null,
    events: [
      {
        name: "Matematica para ingenieros 2",
        start: "2020-03-02 08:00",
        end: "2020-03-02 09:30",
        color: "#D4AF37"
      },
      {
        name: "Comunicación",
        start: "2020-03-02 09:30",
        end: "2020-03-02 10:30",
        color: "#1E88E5"
      },
      {
        name: "Comunicación",
        start: "2020-03-02 11:00",
        end: "2020-03-02 14:00",
        color: "#1E88E5"
      },

      {
        name: "Matematica para ingenieros 2",
        start: "2020-03-03 08:00",
        end: "2020-03-03 09:00",
        color: "#D4AF37"
      },
      {
        name: "Comunicación",
        start: "2020-03-03 09:00",
        end: "2020-03-03 10:30",
        color: "#1E88E5"
      },
      {
        name: "Ciencia Tecnología y ambiente",
        start: "2020-03-03 11:00",
        end: "2020-03-03 14:00",
        color: "#4caf50"
      },

      {
        name: "Comunicación",
        start: "2020-03-04 08:00",
        end: "2020-03-04 10:30",
        color: "#1E88E5"
      },
      {
        name: "Matematica para ingenieros 2",
        start: "2020-03-04 11:00",
        end: "2020-03-04 14:00",
        color: "#D4AF37"
      }
    ],
    colors: [
      "blue",
      "indigo",
      "deep-purple",
      "cyan",
      "green",
      "orange",
      "grey darken-1"
    ],
    names: [
      "Meeting",
      "Holiday",
      "PTO",
      "Travel",
      "Event",
      "Birthday",
      "Conference",
      "Party"
    ],
    type: "custom-daily",
    typeOptions: [
      { text: "Day", value: "day" },
      { text: "4 Day", value: "4day" },
      { text: "Week", value: "week" },
      { text: "Month", value: "month" },
      { text: "Custom Daily", value: "custom-daily" },
      { text: "Custom Weekly", value: "custom-weekly" }
    ],
    mode: "stack",
    modeOptions: [
      { text: "Stack", value: "stack" },
      { text: "Column", value: "column" }
    ],
    weekdays: [1, 2, 3, 4, 5],
    weekdaysOptions: [
      { text: "Sunday - Saturday", value: weekdaysDefault },
      { text: "Mon, Wed, Fri", value: [1, 3, 5] },
      { text: "Mon - Fri", value: [1, 2, 3, 4, 5] },
      { text: "Mon - Sun", value: [1, 2, 3, 4, 5, 6, 0] }
    ],
    intervals: { first: 15, minutes: 30, count: 14, height: 35 },
    intervalsOptions: [
      { text: "Default", value: intervalsDefault },
      {
        text: "Workday",
        value: { first: 16, minutes: 30, count: 15, height: 48 }
      }
    ],
    maxDays: 5,
    maxDaysOptions: [
      { text: "7 days", value: 7 },
      { text: "5 days", value: 5 },
      { text: "4 days", value: 4 },
      { text: "3 days", value: 3 }
    ],
    styleInterval: "default",
    styleIntervalOptions: [
      { text: "Default", value: "default" },
      { text: "Workday", value: "workday" },
      { text: "Past", value: "past" }
    ],
    color: "primary",
    colorOptions: [
      { text: "Primary", value: "primary" },
      { text: "Secondary", value: "secondary" },
      { text: "Accent", value: "accent" },
      { text: "Red", value: "red" },
      { text: "Pink", value: "pink" },
      { text: "Purple", value: "purple" },
      { text: "Deep Purple", value: "deep-purple" },
      { text: "Indigo", value: "indigo" },
      { text: "Blue", value: "blue" },
      { text: "Light Blue", value: "light-blue" },
      { text: "Cyan", value: "cyan" },
      { text: "Teal", value: "teal" },
      { text: "Green", value: "green" },
      { text: "Light Green", value: "light-green" },
      { text: "Lime", value: "lime" },
      { text: "Yellow", value: "yellow" },
      { text: "Amber", value: "amber" },
      { text: "Orange", value: "orange" },
      { text: "Deep Orange", value: "deep-orange" },
      { text: "Brown", value: "brown" },
      { text: "Blue Gray", value: "blue-gray" },
      { text: "Gray", value: "gray" },
      { text: "Black", value: "black" }
    ],
    shortIntervals: true,
    shortMonths: false,
    shortWeekdays: false,
    daysOfWeek: [
      "Lunes",
      "Martes",
      "Miercoles",
      "Jueves",
      "Viernes",
      "Sabado",
      "Domingo"
    ]
  }),
  computed: {
    intervalStyle() {
      return stylings[this.styleInterval].bind(this);
    },
    hasIntervals() {
      return (
        this.type in
        {
          week: 1,
          day: 1,
          "4day": 1,
          "custom-daily": 1
        }
      );
    },
    hasEnd() {
      return (
        this.type in
        {
          "custom-weekly": 1,
          "custom-daily": 1
        }
      );
    }
  },
  methods: {
    /* viewDay ({ date }) {
      this.start = date
      this.type = 'day'
    }, */
    getEventColor(event) {
      return event.color;
    },
    showIntervalLabel(interval) {
      return interval.minute === 0;
    },
    /* getEvents ({ start, end }) {
      const events = []

      const min = new Date(`${start.date}T00:00:00`)
      const max = new Date(`${end.date}T23:59:59`)
      const days = (max.getTime() - min.getTime()) / 86400000
      const eventCount = this.rnd(days, days + 20)

      for (let i = 0; i < eventCount; i++) {
        const allDay = this.rnd(0, 3) === 0
        const firstTimestamp = this.rnd(min.getTime(), max.getTime())
        const first = new Date(firstTimestamp - (firstTimestamp % 900000))
        const secondTimestamp = this.rnd(2, allDay ? 288 : 8) * 900000
        const second = new Date(first.getTime() + secondTimestamp)

        events.push({
          name: this.names[this.rnd(0, this.names.length - 1)],
          start: this.formatDate(first, !allDay),
          end: this.formatDate(second, !allDay),
          color: this.colors[this.rnd(0, this.colors.length - 1)],
        })
      }

      this.events = events
    }, */
    rnd(a, b) {
      return Math.floor((b - a + 1) * Math.random()) + a;
    },
    formatDate(a, withTime) {
      return withTime
        ? `${a.getFullYear()}-${a.getMonth() +
            1}-${a.getDate()} ${a.getHours()}:${a.getMinutes()}`
        : `${a.getFullYear()}-${a.getMonth() + 1}-${a.getDate()}`;
    },
    getDay(date) {
      let i = new Date(date.date).getDay();
      return this.daysOfWeek[i];
    }
  }
};
</script>

<style>
.v-calendar .v-event-timed-container {
  margin-right: 0;
}

.v-calendar-daily__scroll-area::-webkit-scrollbar {
  transition: all 1.5s ease;
  width: 0.8em;
}

.v-calendar-daily__scroll-area::-webkit-scrollbar-track {
  transition: all 1.5s ease;
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}

.v-calendar-daily__scroll-area::-webkit-scrollbar-thumb {
  transition: all 1.5s ease;
  background-color: #ba0f22;
  outline: 1px solid slategrey;
}
@media screen and (max-width: 600px) {
  .v-calendar-daily_head-weekday {
    font-size: 7.5px;
    text-align: center;
    text-transform: uppercase;
  }
  .v-btn--fab.v-size--default {
    height: 25px;
    width: 25px;
    margin-top: 10px;
  }
  .v-calendar-daily__head {
    margin-right: 0;
  }
  /* .v-calendar .v-calendar .v-calendar-daily .v-calendar-daily_head-day, .v-calendar .present {
        width: 90px;
    } */
  /* .theme--light.v-calendar-daily .v-calendar-daily_head-day, .v-calendar-daily_head-day .v-calendar-daily_head-weekday, .v-calendar-daily_head-day .v-calendar-daily_head-day-label{
        width: 90px;
    } */
  .v-calendar .v-calendar-daily__day {
    width: 65px;
  }
  .v-calendar .v-calendar-daily__day-interval {
    width: 65px;
  }
  .v-calendar .v-event-timed-container {
    width: 65px;
  }
  .v-calendar .v-event-timed {
    /* width: 90px; */
    font-size: 10px;
  }

  .v-calendar .v-event-timed strong {
    font-size: 9px;
    /* max-width: 85px; */
    text-align: justify;
    white-space: pre-wrap;
  }
  .v-calendar-daily__intervals-body {
    width: 15px;
  }
  /* .v-calendar-daily__scroll-area::-webkit-scrollbar {
        transition: all 1.5s ease;
        width: .15em;
  } */
}

